import React from 'react';
import { Dialog, DialogContent, DialogContentText } from "@material-ui/core";
import { baseImgURL } from 'assets/Constant';
import { MxButton } from '../../atoms/MxButton';
import { MdClose } from "react-icons/md";
import AdminServices from 'services/api/AdminServices';
import { toast } from 'react-toastify';

export default function MxDialog({ props, handleToClose, open }) {

    const [phone,setPhone] = React.useState('')

    const onNextBtnPressed = async () => {
		if (phone.length === 10) {
			const res = await AdminServices.sendAppLinks(phone).then(res => res)
			if (res.type) {
				toast.success("Message sent")
                setPhone('')
                handleToClose()
			} else {
				toast.error("Try again ...");
			}
		}
	}
    return (
        <Dialog open={open} onClose={handleToClose} fullWidth={true} maxWidth="sm"  >

            <DialogContent style={{}}>
                <DialogContentText>
                    <div style={{ display: "flex", flexDirection: "row", justifyContent: "space-between", alignItems: "center" }}>
                        <div style={{ width: "80%" }}>
                            <div style={{ fontSize: "26px", fontWeight: "500", lineHeight: "36px", whiteSpace: "pre", color: "#333" }}>
                                Get app download{'\n'}link on your phone
                            </div>
                            <div className="mxMinSubHead" style={{ fontWeight: "500", marginTop: "20px", }}>Enter your phone number</div>
                            <div style={{ display: "flex", flexDirection: "row", alignItems: "center" }}>
                                <input
                                    className="normalInput"
                                    style={{ textTransform: 'capitalize', width: "50%" }}
                                    type="text"
                                    maxLength={10}
                                    placeholder="9090909090"
                                    id="Full-name"
                                    value={phone}
                                    onChange={(num) => {
                                        const newPhone =  num.target.value.replace(/\D/g, '');
                                        setPhone(newPhone);
                                    }}
                                />
                                {/* <div > */}
                                <MxButton
                                    name="Get link"
                                    style={{
                                        padding: "7.5px 15px 7.5px 15px",
                                        marginTop: "0px",
                                        borderRadius: "8px",
                                        marginLeft: "20px",
                                        marginBottom: "5px"
                                    }} 
                                    onPress={()=>onNextBtnPressed()}
                                        />
                                {/* </div> */}
                            </div>

                        </div>

                        <div style={{ display: "flex", flexDirection: "column", height: "100%" }}>
                            <div style={{ borderLeft: "1px dashed #E5E5E5", height: "100px" }}></div>
                            <div style={{ flex: 1, marginLeft: "-8px" }}>or</div>
                            <div style={{ borderLeft: "1px dashed #E5E5E5", height: "100px" }}></div>
                        </div>

                        <div style={{ display: "flex", flexDirection: "column", justifyContent: "space-around", alignItems: "center", width: "40%" }}>
                            <div className="mxMinSubHead" style={{ color: "#333", fontWeight: "500", whiteSpace: "pre", lineHeight: "20px", textAlign: "center", marginBottom: "10px" }}>
                                Scan QR code{'\n'}to download
                            </div>

                            <img src={baseImgURL+"QR-code_new.webp"} style={{ height: "96px", width: "96px" }} />

                        </div>
                    </div>

                </DialogContentText>
            </DialogContent>
            <div style={{
                display: "flex",
                position: "absolute",
                // borderRadius: "100px",
                // backgroundColor: "#000",
                // padding: "5px",
                top: "15px",
                right: "20px",
                // zIndex: "21",
                // height: "35px",
                // width: "35px",
                // justifyContent: "center"
            }}>
                <MdClose onClick={handleToClose} size={20} style={{ color: "#000", cursor: "pointer" }} />
            </div>



            {/* <DialogActions>
                    <Button onClick={handleToClose}
                        color="primary" autoFocus>
                        Close
                    </Button>
                </DialogActions> */}
            <style type="text/css">
                {`
            .MuiPaper-rounded {
                    border-radius: 32px;
                    // min-height: 250px;
                }
            .MuiDialogContent-root {
                flex: 1 1 auto;
                padding: 18px 44px;
                overflow-y: auto;
                // -webkit-overflow-scrolling: touch;
                }
            `}
            </style>

        </Dialog>
    );
}